*{
    font-family: 'Inter', sans-serif;
    box-sizing: border-box;
    background: #23252C;
    
}

.body{
    background: #1A1B21;
}

info{
    width: 300px;
    padding-top: 45px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin:0;
}

.info_photo{
    width: 300px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    
}

.info_name{
    color: white;
    font-size: 700;
    align-self: center;
    background: #1A1B21;
}

.info_position{
    color: #F3BF99;
    font-size: 13px;
    font-weight: 400;
    align-self: center;
    background: #1A1B21;
}

.info_website{
    color: #F5F5F5;
    font-size: 10px;
    font-weight: 400;
    align-self: center;
    background: #1A1B21;
}